import * as React from 'react';

import connection from "../../Connection";

class Submit extends React.Component<any, { title: string, body: string }> {
    constructor(props: any){
        super(props);
        this.state = {title: "", body: ""};
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event: any) {
        console.log(this.state.body, this.state.title);
        connection.mutate({
            query: `{
                addPost(title: $title, body: $body)
            }`,
            variables: {
                "title": this.state.title,
                "body": this.state.body
            }
        })
    }

    render() {
        return (
            <div>
                <label>
                    Title: <input id="title" type="text" onChange={(evt) => {this.setState({title: evt.target.value});}}/>
                    <br/>
                    Body: <input id="body" type="text" onChange={(evt) => {this.setState({body: evt.target.value});}}/>
                </label>
                <br/>
                <input type="submit" value="Submit" onClick={this.handleSubmit}/>
            </div>
        )
    }
}

export default Submit;


