import * as React from 'react';
import { GraphQLData } from 'thunder-react';
import { IPostsQueryResult } from '../../App'

class Posts extends React.Component<GraphQLData<IPostsQueryResult>, any> {
    constructor(props: GraphQLData<IPostsQueryResult>) {
        super(props);
    }

    render() {
        if (!this.props.data.value) {
            return null
        }
        const posts = this.props.data.value.posts;
        return (
            <div>
                {posts.map((args: any) => (
                    <div key={args.title}>
                        <h1>{args.title}</h1>
                        <p>{args.body}</p>
                        <hr/>
                    </div>
                ))}
            </div>
        )
    }
}

export default Posts;


