import * as React from 'react';
import { graphql, GraphQLData, ThunderProvider } from 'thunder-react'; 
import './App.css';

import logo from './logo.svg';

import connection from "./Connection";
import Posts from "./components/posts/Posts";
import Submit from "./components/submit/Submit";

export interface IPostsQueryResult {
  output: string;
  posts: any[];
};

const ConnectedPosts = graphql<GraphQLData<IPostsQueryResult>, IPostsQueryResult>(
  Posts,
  `query {
    posts {
      age
      title
      body
    }
  }`
)

class App extends React.Component {
  public render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to Thunder</h1>
        </header>
        <ThunderProvider connection={connection}>
          <div className="App-intro">
            <br />
            <Submit />
            <ConnectedPosts />
          </div>
        </ThunderProvider>
      </div>
    );
  }
}

export default App;



